body {
  margin: 0;
  padding: 0%;
}

.titulo {
  color: rgb(5, 5, 96);
  margin-bottom: 60px;
}

/* div dos bilhetes */
.caixaBilhete {
  width: 100%;
  height: 150px;
  background-color: white;
  border-radius: 20px;
  box-shadow: 0 4px 10px 2px rgba(0, 0, 0, 0.1);
  border: 2px solid rgb(9, 9, 140);

  padding-right: 30px;
  padding-left: 30px;
  padding-top: 10px;
  /*padding-bottom: 20px;*/

  box-sizing: border-box;

  transition: 0.4s;
  margin-bottom: 15px;

  div {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
  }
}

