}*/

/* animação de cor com os botoes*/
.botaoTransicao {
  display: inline-block;
  transition-duration: 0.3s;
  cursor: pointer;
}

/* botao de excluir bilhetes do carrinho */
.btCarrinho {
  content: '';
  display: inline-block;
  border: none;
  border-color: transparent;
  outline: none;
  background-size: contain;
  background-color: transparent;
  width: 28px;
  height: 30px;
  border: none;
  cursor: pointer;
  transition: 0.4s;
}

.btCarrinho:hover {
  transform: scale(1.1);
}



.column {
  background-color: transparent;
}

.left {
  width: 70%;
}

.right {
  width: 30%;
  max-width: 30%;
}

.row {
  display: flex;
  box-sizing: border-box;
  margin: 0;
  width: 100%;
  padding: 40px;
  gap: 40px;
}









/* caixa sobre o preco total */
.caixaTotal {
  background-color: rgb(231, 231, 231);
  width: 100%;
  height: 200px;
  border-radius: 10px;

  margin-top: 110px;
  padding-top: 20px;
  padding-left: 20px;
  padding-right: 20px;
  box-sizing: border-box;
  display: flex;
  flex-direction: column;

  hr {
    border: 1px solid black;
    margin-left: 0px;
    margin-right: 10px;
    margin-top: 10px;
