.botaoVerificar {
  background-color: rgb(4, 4, 77);
  color: white;
  border-radius: 20px;
  padding-top: 10px;
  padding-bottom: 10px;
  padding-right: 40px;
  padding-left: 40px;
  justify-self: center; 
  align-self: center;
  justify-content: center;
  justify-items: center;
